@custom-media --wide-vp (width >= 1024px);
@custom-media --not-wide-vp (width <= 1023.9px);
@custom-media --middle-vp (768px <= width < 1023.9px);
@custom-media --small-vp (width <= 767.9px);
@custom-media --not-small-vp (width >= 768px);
@custom-media --x2 (min-resolution: 2dppx);
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-condensed.woff2") format("woff2"), url("../fonts/roboto-condensed.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
:root {
  --regular-font: "PT Mono", "Courier New", monospace;
  --accent-font: "Roboto Condensed", "Arial", sans-serif;
  --medium-font: "Roboto", "Arial", sans-serif;
}

:root {
  --color-white: #ffffff;
  --color-base-light: #f2f5f6;
  --color-base-dark: #041827;
  --color-accent: #00b2ff;
  --color-bg-base: #f8fdff;
  --color-bg-dark: #041827;
  --color-gradient-header: rgba(4, 24, 39, 0);
}

.page {
  height: 100%;
  font-family: var(--regular-font);
  font-weight: 400;
  color: var(--color-base-dark);
  scroll-behavior: smooth;
}

.page ul {
  list-style-type: none;
  padding-left: 0;
}

.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--color-bg-base);
}

.page__visual-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (--wide-vp) {
  .page__wrapper-center {
    box-sizing: border-box;
    max-width: 1220px;
    padding: 0 50px;
    margin: 0 auto;
  }
}
@media (--middle-vp) {
  .page__wrapper-center {
    padding: 0 50px;
  }
}
@media (--small-vp) {
  .page__wrapper-center {
    padding: 0 25px;
  }
}

.page-main {
  flex-grow: 1;
}

.page-header {
  position: fixed;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 35px;
  background-image: linear-gradient(180deg, var(--color-base-dark) 84.9%, var(--color-gradient-header) 100%);
}

.page-header__wrapper-grid {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 20px;
}

.page-header__contacts a {
  color: var(--color-base-light);
  font-style: normal;
  font-size: 16px;
  line-height: 18px;
}

.page-header__logo-link svg {
  grid-column: 1/2;
  grid-row: 1/-1;
}

.page-header__logo-link b {
  font-family: var(--accent-font);
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  color: var(--color-base-light);
  grid-column: 2/-1;
  grid-row: 1/2;
  padding-top: 5px;
}

.page-header__logo-link i {
  font-family: var(--medium-font);
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: var(--color-white);
  opacity: 70%;
  grid-column: 2/-1;
  grid-row: 2/-1;
  margin-top: -10px;
}

.page-header__logo-link {
  display: grid;
  width: fit-content;
  grid-template-columns: auto auto;
  grid-template-rows: min-content auto;
  column-gap: 15px;
}

.page-header__contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  padding-top: 15px;
}

.page-header__button {
  font-size: 14px;
  line-height: 16px;
  width: 160px;
  height: fit-content;
  min-height: 40px;
  padding: 2px 10px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--color-base-light);
  border: 1px solid var(--color-base-light);
  border-radius: 3px;
  background-color: transparent;
}

/*style*/
.button {
  font-family: var(--accent-font);
  font-weight: 700;
  text-decoration: none;
  user-select: none;
}